import React from 'react';
import LandingPage from './LandingPage'
import Services from './Services'
import Contact from './Contact'

class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      home: true,
      services: false,
      contact: false
    }
    this.changePage = this.changePage.bind(this)
  }

  changePage(event) {
    const { id } = event.target;
    let pages = ['home', 'services', 'contact'];
    pages.splice(pages.indexOf(id), 1);
    const other1 = pages[0];
    const other2 = pages[1];
    this.setState({
      [id]: true,
      [other1]: false,
      [other2]: false
    });
  }

  render(){
    return this.state.home === true 
        ? (<LandingPage navbar={this.props.navbar} />)
        : this.state.services === true 
        ? (<Services navbar={this.props.navbar} />)
        : this.state.contact === true
        ? (<Contact navbar={this.props.navbar} />)
        : (<LandingPage navbar={this.props.navbar} />)
  }
}

export default App;
